////////////////////////////////////////////////////
//imports
///////////////////////////////////////////////////
@import "./scss/Variables.scss";
@import "./scss/Header.scss";
@import "./scss/About.scss";
@import "./scss/Portfolio.scss";
@import "./scss/Contact.scss";
@import "./scss/Skills.scss";
@import "./scss/Card.scss";

////////////////////////////////////////////////////
//Basisc styling
///////////////////////////////////////////////////
* {
  box-sizing: border-box;
  color: $clr-prm;
  font-family: $font-snd;
}

body {
  background-color: $clr-snd;
}

.container {
  max-width: 90%;
  margin-left: auto;
  margin-right: auto;
}

a:hover {
  cursor: pointer;
}

footer {
  display: block;
  height: 30px;
  width: 100%;
  text-align: center;
  background-color: $clr-acc;
  margin-top: 5vh;
  bottom: 0;
}

.title {
  color: $clr-title;
  font-size: clamp(36pt, 8vw, 72pt);
  font-family: $font-prm;
  text-transform: uppercase;
  margin-top: 50px;
}

h3 {
  margin: 0;
}

.icon {
  transition: 0.5s ease-out;
  overflow: visible;
}

.icon:hover {
  cursor: pointer;
  transform: scale(1.2);
  transition: ease-in-out 0.3s;
}

.back-up {
  display: block;
  width: 48px;
  height: 48px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  border: solid 2px $clr-acc;
  border-radius: 20%;
  padding: 5px;
  transition: 0.2s ease-out;
  z-index: 100;
}

.back-up:hover {
  transform: scale(1.2);
  border: none;
  background-color: $clr-prm;
  fill: $clr-snd;
}

////////////////////////////////////////////////////
//Media Queries
///////////////////////////////////////////////////
@media (min-width: $tablet) {
  .container {
    max-width: 80%;
  }

  .header-left {
    width: 100%;
    padding-right: 50px;
  }

  .header-right {
    display: flex;
    width: 40%;
    height: 90%;
  }

  .icons {
    svg {
      margin: 36px;
    }
  }

  .title {
    margin-top: 100px;
  }

  .contact-left {
    width: 50%;
  }
  .contact-right {
    width: 50%;
  }

  .about-left {
    width: 50%;
  }
  .about-right {
    width: 50%;
  }
}

@media (min-width: $desktop) {
  .navbar {
    display: block;
  }

  .hamburger {
    display: none;
  }
  .container {
    max-width: 75%;
  }

  .icons {
    svg {
      margin: 48px;
    }
  }
  .skills-flex {
    width: 44%;
  }
  .soft-skills {
    padding: 20px;
  }
}

@media (min-width: $desktop-lg) {
  .container {
    max-width: 65%;
  }
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}
